.single-content{
   &__container{
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
   }
   &__content{
      min-height: inherit;
      position: relative;
      padding: 30px 20px;
      width: 70%;
      background: #fff;
      box-shadow: 0 2px 2px rgba(0, 0, 0, 0.13);
      
      &-img{
         width: 100%;
         height: 250px;
         margin: 0 auto;
         margin-bottom: 5px;
         & img{
            width: 100%;
            height: 100%;
            object-fit: cover;
         }
      }
      & span.date,
      & span.author{
         display: inline-block;
         margin-right: 10px;
         color: var(--color-text);
         opacity: 0.8;
         margin: 10px 0;
         & i{
            display: inline-block;
            margin-right: 5px;
         }
      }
      &-text{
         padding-top: 30px;
         font-family: 'Museosans', sans-serif;
         font-size: 16px;
         color: var(--color-text);
         line-height: 1.3;
         border-top: 1px solid rgba(17, 17, 17, 0.2);
      }
   }

   &__sidebar{
      width: 20%;
      background: #f5f5f5;
      & .sidebar-item ul li{
         display: block;
         padding: 10px 0;
         & span.date{
            opacity: 0.7;
            font-size: 13px;
         }
         & a{
            display: block;
            transition: color 0.25s;
            &:hover{
               color: var(--color-text);
               text-decoration: underline;
            }
         }
      }
   }
}