.info{
   &__container{
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
   }
   & .news__slider{
      position: relative;
      width: 60%;
      height: 470px;
      overflow: hidden;
      & h2{
         margin-bottom: 20px;
      }
      &-slide{
         position: relative;
         
         & .news__card{
            height: 400px;
         padding: 15px;
         text-align: center;
         background: #fff;
         display: flex;
         flex-direction: column;
         justify-content: space-around;
         align-items: center;
            & img{
               display: inline-block;
               width: 300px;
               height: 300px;
               margin-bottom: 10px;
            }
            & h4{
               flex-grow: 1;
               display: block;
               color: var(--color-text);
               margin-bottom: 15px;
            }
            & .card-bottom{
               height: 10%;
               width: 100%;
               padding: 10px;
               display: flex;
               justify-content: space-between;
               align-items: center;
               & span{
                  opacity: 0.7;
                  & i{
                     display: inline-block;
                     margin-right: 5px;
                  }
               }
               & a{
                  padding: 5px;
                  background: var(--color-text);
                  color: #fff;
               }
            }
         }
      }
   }
   &__anons{
      width: 30%;
      height: 520px;
      overflow: hidden;
      & h3{
         display: block;
         text-align: center;
      }
      & ul{
         margin-top: 25px;
         position: relative;
         width: 100%;
         height: 100%;
         border-top: 2px solid var(--color-green);
         & i.fa-medkit{
            position: absolute;
            top: -24px;
            left: 50%;
            font-size: 36px;
            transform: translateX(-50%);
            border-radius: 50%;
            padding: 10px;
            background: #f5f5f5;
            border: 2px solid var(--color-green);
            color: var(--color-text);
            z-index: 1;
         }
         & li{
            position: relative;
            display: block;
            width: 100%;
            height: 20%;
            background: var(--color-text);
            padding: 15px 10px;
            display: flex;
            flex-direction: column;
            justify-content: space-between;
            align-items: flex-start;
            border-bottom: 1px solid rgba(0, 0, 0, 0.1);
            & span.data{
               display: inline-block;
               opacity: 0.7;
               color: #fff;
               & i{
                  position: relative;
                  display: inline-block;
                  margin-right: 5px;
               }
            }
            & a{
               display: inline-block;
               & h4{
                  color: #fff;
                  &:hover{
                     text-decoration: underline;
                  }
               }
            }
         }
      }
   }
}