.header {
   width: 100%;
 }
 
 .header__container {
   height: 100%;
   padding: 0;
 }
 
 .header__top {
   height: 50px;
   width: 100%;
   background: rgb(65, 65, 65);
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
 }
 
 .header__top-container {
   width: 1200px;
   padding: 0 10px;
   margin: 0 auto;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
 }
 
 .header__social {
   width: 120px;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
 }
 
 .header__social a.social-icon {
   font-size: 26px;
 }
 
 .header__social a i {
   color: #fff;
   transition: color 250ms;
   &:hover{
     color: var(--color-green);
   }
 }
 
 .header__info {
   width: auto;
   text-align: center;
   padding: 0 10px;
 }
 
 .header__info span {
   display: block;
   margin-bottom: 5px;
 }
 
 .header__info span:nth-child(1) {
   color: #215c89;
 }
 
 .header__info a {
   display: block;
   font-size: 15px;
   color: #215c89;
   margin-bottom: 5px;
 }
 
 .header__info a:hover {
   text-decoration: underline;
 }
 
 .header__info a.email i {
   margin-right: 5px;
 }
 
 .header__lang a.lang {
   position: relative;
   display: inline-block;
   color: #fff;
 }
 
 .header__lang a.lang:not(:last-child) {
   margin-right: 7px;
 }
 
 .header__lang a.lang:not(:last-child):before {
   content: '';
   position: absolute;
   top: 0;
   right: -25%;
   width: 1px;
   height: 100%;
   background: rgba(255, 255, 255, 0.288);
 }
 
 .header__lang a.lang:hover {
   text-decoration: underline;
 }
 
 .header__version {
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: center;
       -ms-flex-pack: center;
           justify-content: center;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
   width: auto;
   height: 50px;
   color: var(--color-text);
   background: var(--color-green);
   padding: 0 5px;
 }
 
 .header span.local {
   color: #fff;
 }
 
 .header span.local i {
   margin-right: 5px;
 }
 
 .header__content {
   padding: 0 10px;
   position: relative;
   height: 100px;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: space-between;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
           background: linear-gradient(to right, #f9f9f9,#fff);
 }
 
 .header__logo{
  position: relative;
   display: block;
   display: flex;
   align-items: center;
  
   & h1{
     width: auto;
    display: block;
    font-size: 26px;
    color: #215c89;
    text-transform: uppercase;
    & span{
      display: block;
      margin-left: 15px;
      font-size: 18px;
    }
   }
 }
 
