 .header__link {
   width: 150px;
   height: 80px;
   overflow: hidden;
   opacity: 0.8;
   transition: opacity 0.3s;
   & img{
    width: 100%;
    height: 100%;
   }
   &:hover{
     opacity: 1;
   }
 }
 
 .header__time {
   text-align: center;
   margin-right: 15px;
   padding: 5px 0;
   font-size: 18px;
   height: 100%;
   background: var(--color-text);
   color: #fff;
   & i{
     display: block;
     text-align: center;
     font-size: 26px;
   }
   & span{
     display: inline-block;
     padding: 5px 10px;
   
   }
 }
 
 .burger{
    display: none;
 }
