.about{
   &__container{
      padding: 50px 0;
      position: relative;
      display: flex;
      justify-content: space-between;
      align-items: flex-start;
      background: #fff;
      &:before{
         content: '';
         position: absolute;
         bottom: 0;
         left: 0;
         width: 100%;
         height: 100%;
         background: linear-gradient(to right,transparent,#fff), url(https://st3.depositphotos.com/1157091/13545/v/950/depositphotos_135458158-stock-illustration-abstract-background-medical.jpg);
         background-size: contain;
         opacity: 0.5;
      }
   }
   &__info{
      position: relative;
      width: 70%;
      padding: 10px 15px;
      // background-color: #fff;
      
      z-index: 1;
      // &:before{
      //    content: '';
      //    position: absolute;
      //    top: 0;
      //    left: 0;
      //    width: 100%;
      //    height: 100%;
      //    background: url(https://www.kayareklamtabela.com/wp-content/uploads/2018/02/slayt-bg.jpg);
      //    background-size: contain;
      //    z-index: -1;
      // }
      & h2{
         margin-bottom: 25px;
      }
      & img{
         display: block;
         width: 250px;
         height: 150px;
         float: left;
         margin-right: 10px;
      }
      & h4{
         // display: inline-block;
         margin-bottom: 10px;
         font-size: 22px;
      }
      & p{
         font-family: 'MuseoSans', sans-serif;
         font-weight: 500;
         font-size: 15px;
         line-height: 1.5;
         margin-bottom: 10px;
      }
      & a{
         display: block;
         width: 130px;
         margin-left: auto;
      }
   }
   &__right{
      position: relative;
      width: 30%;
      height: 100%;
   }
   &__blog{
      position: relative;
      text-align: center;
      & h3{
         position: relative;
         display: inline-block;
         margin-bottom: 15px;
         &:before{
            content: '';
            position: absolute;
            bottom: -5px;
            left: 50%;
            width: 50%;
            height: 1px;
            background: var(--color-green);
            transform: translateX(-50%);
          }
      }
      & .name{
         color: var(--color-text);
         font-size: 18px;
         margin: 10px 0;
      }
      & a{
         padding: 5px 10px;
         
      }
   }
   &__sprav{
      padding: 10px 15px;
      text-align: center;
      background-color: var(--color-blue);
      border-radius: 4px;
      box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
      & .row{
         margin: 15px 0;
         text-align: center;
         & a{
            display: block;
            font-size: 18px;
            color: #fff;
            margin-bottom: 10px;
            & i.fa{
               display: inline-block;
               margin-right: 10px;
            }
         }
         & span{
            display: block;
            font-size: 18px;
            color: #fff;
            & i.fa{
               display: inline-block;
               margin-right: 10px;
            }
         }
      }
   }
}