.team{
   &__container{
      position: relative;
      background: #fff;
      overflow: hidden;
      z-index: 5;
      &::before{
         content: '';
         position: absolute;
         top: 0;
         left: 0;
         width: 55%;
         height: 100%;
         background: rgba(33, 92, 137, 0.1);
         transform: skewX(-15deg) translateX(-20%);
         z-index: -1;
      }
      & h2.section-title{
         text-align: center;
         &:before{
            left: 50%;
            transform: translateX(-50%);
         }
         &:after{
            content: none;
         }
      }
      & .team__sub{
         display: block;
         width: 100%;
         text-align: center;
         margin: 20px 0;
         font-size: 18px;
      }
   }
   &__slider{
      position: relative;
      margin-top: 25px;
      width: 100%;
      height: 400px;
      overflow: hidden;
      &-slide{
         position: relative;
         width: 250px;
         height: 100%;
         display: flex;
         flex-direction: column;
         justify-content: flex-start;
         align-items: center;
         transition: box-shadow 0.3s linear, transform 0.3s ease-in;
         & img{
            display: block;
            width: 100%;
            height: 300px;
            margin-bottom: 10px;
          
         }
         & span.name{
            display: block;
            padding: 0 10px;
            width: 100%;
            color: var(--color-text);
            text-align: center;
            font-size: 18px;
            margin-bottom: 10px;
            transition: color 0.4s;
         }
         & span.pos{
            display: block;
            padding: 0 10px;
            font-size: 15px;
            text-align: center;
            width: 100%;
            color: #111;
            transition: color 0.4s;
         }
         &:hover{
            background: var(--color-text);
            box-shadow: 0 2px 3px rgba(0, 0, 0, 0.2);
            transform: scale(1.1);
            & span{
               color: #fff;
            }
         }
      }
   }
}

.team-prev,
.team-next{
   background: var(--color-text);
   width: 50px;
   height: 50px;
   opacity: 0.5;
   transition: opacity 0.3s;
   &:hover{
      opacity: 1;
   }
   &:after{
      color: #fff;
   }
}
.team-prev{
   left: 0;
}
.team-next{
   right: 0;
}