.faq{
   &__container{
      position: relative;
      height: 100%;
      display: flex;
      justify-content: center;
      align-items: center;
      flex-wrap: wrap;
      &:before{
         content: '';
         position: absolute;
         top: 0;
         left: 0;
         width: 100%;
         height: 100%;
         background: linear-gradient(to right,#f5f5f5c0,#f5f5f594), url(https://img.pngio.com/image-result-for-blockchain-wallpaper-851925-png-images-pngio-blockchain-wallpaper-png-2560_1440.png) center no-repeat fixed;
         background-size: cover;
      }
      & h2.section-title{
         margin-bottom: 25px;
      }
   }
   &__accordion{
      width: 50%;
   }
   &__slider{
      width: 440px;
      height: 440px;
      &-slide{
         position: relative;
         & img{
            width: 100%;
            height: auto;
         }
      }
   }
}