.hero {
   width: 100%;
   height: 300px;
 }
 
 .hero__container {
   position: relative;
   padding: 0;
 }
 
 .hero__content {
   position: absolute;
   top: 0;
   left: 0;
   width: 60%;
   height: 100%;
   background: linear-gradient(to right, var(--color-text),#215c898c,transparent);
   padding-left: 70px;
   display: flex;
   flex-direction: column;
   justify-content: space-between;
   align-items: flex-start;
   overflow: hidden;
   z-index: 2;
   & .info__box{
    padding: 20px 15px;
    text-align: center;
    border-radius: 4px;
    & h3.block-title{
      position: relative;
      color: var(--color-text);
      padding: 5px 10px;
      background: var(--color-green);
      // &:before{
      //   content: '';
      //   position: absolute;
      //   bottom: -5px;
      //   left: -10%;
      //   height: 100%;
      //   width: 120%;
      //   background: linear-gradient(to right,var(--color-green),var(--color-green),var(--color-green));
      //   z-index: -1;
      // }
    }
    & .row{
       margin: 15px 0;
       text-align: center;
       & a{
          display: block;
          font-size: 18px;
          color: #fff;
          margin-bottom: 10px;
          & i.fa{
             display: inline-block;
             margin-right: 10px;
          }
       }
       & span{
          display: block;
          font-size: 18px;
          color: #fff;
          & i.fa{
             display: inline-block;
             margin-right: 10px;
          }
       }
    }
 }
 & a.btn{
   margin-bottom: 20px;
 }
 }
 
 .hero__content::before {
   content: '';
   position: absolute;
   top: 20px;
   left: 0;
   width: 40%;
   height: 100%;
   background: url(https://svgsilh.com/svg/30591-03a9f4.svg) center no-repeat;
   background-size: 70%;
   opacity: 0.2;
   z-index: -1;
 }
 
 .main-slider {
   position: relative;
   width: 100%;
   height: 300px;
   overflow: hidden;
   z-index: 1;
 }
 
 .main-slider__slide {
   width: 100%;
   position: relative;
 }
 
 .main-slider__slide img {
   position: absolute;
   top: 50%;
   left: 50%;
   width: 100%;
   height: 150%;
   filter: brightness(150%);
   -webkit-filter: brightness(70%);
   -webkit-transform: translate(-50%, -50%);
           transform: translate(-50%, -50%);
 }
 
 .main-links {
   position: relative;
   border-top: 1px solid var(--color-green);
 }
 
 .main-links__container {
   height: 100%;
   padding: 0;
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
 }
 
 .main-links__item {
   position: relative;
   display: block;
   width: 25%;
   height: 100px;
   background: var(--color-text);
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: justify;
       -ms-flex-pack: justify;
           justify-content: center;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
 }
 
 .main-links__item::before {
   content: '';
   position: absolute;
   top: 0;
   left: 0;
   width: 100%;
   height: 100%;
   background: rgba(20, 192, 250, 0.2);
   -webkit-transition: width 200ms;
   transition: width 200ms;
   z-index: 2;
   opacity: 0;
 }
 
 .main-links__item:not(:last-child)::after {
   content: '';
   position: absolute;
   top: 0;
   right: 0;
   width: 1px;
   height: 100%;
   background: -webkit-gradient(linear, left top, left bottom, from(transparent), color-stop(#fff), to(transparent));
   background: linear-gradient(to bottom, transparent, #fff, transparent);
   z-index: 10;
 }
 
 .main-links__item i.fa {
   width: 40%;
  //  text-align: center;
   display: flex;
   justify-content: center;
   align-items: center;
   width: 70px;
   height: 70px;
   margin-left: 10px;
   font-size: 36px;
   color: var(--color-green);
   padding: 10px;
   border-radius: 50%;
   background: #fff;
   margin-right: 10px;
   transition: 0.5s;
 }
 
 .main-links__item span {
   display: -webkit-box;
   display: -ms-flexbox;
   display: flex;
   -webkit-box-pack: center;
       -ms-flex-pack: center;
           justify-content: center;
   -webkit-box-align: center;
       -ms-flex-align: center;
           align-items: center;
           text-align: center;
   width: 50%;
   height: 100%;
   color: #fff;
   font-size: 15px;
   z-index: 10;
   transition: text-shadow 0.3s;
 }
 
 .main-links__item:hover i.fa {
   display: none;
 }
 
 .main-links__item:hover span {
   width: 100%;
   color: #fff;
   font-size: 18px;
   text-shadow: 0 5px 10px #215c89,
  0 5px 12px #215c89;
 }
 
 .main-links__item:hover::before {
   opacity: 1;
 }
 